.item {
    width: 890px;
    height: 40px;
    border-bottom: 2px solid gray;
    line-height: 40px;
}
.user_ava {
    width: 30px;
    height: 30px;
    display: block;
    float: left;
    margin: 5px;
}
.user_ava img {
    width: 30px;
    height: 30px;
}
.user_pp {
    width: 65px;
    margin-left: 10px;
    display: block;
    float: left;
}
.pp_1 {
    font-size: 16px;
    color: grey;
    font-weight: bold;
}
.pp_2 {
    font-size: 12px;
    color: gray;
}
.topic_r {
    height: 20px;
    width: 30px;
    margin: 0 10px;
    float: left;
    display: block;
    background: rgb(93, 145, 16);
    margin-top: 10px;
    border-radius: 5px;
    line-height: 20px;
}
.topic_r p {
    font-size: 12px;
    color: white;
    margin-left: 3px;
}
.top_title {
    width: 600px;
    float: left;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.last {
    height: 40px;
    line-height: 40px;
    margin-left: 40px;
    display: block;
    float: left;
}
.last img {
    margin-top: 10px;
    width: 20px;
    height: 20px;
}
.lasttime {
    height: 40px;
    margin-left: 10px;
    font-size: 12px;
    line-height: 40px;
    display: block;
    float: left;
}
.b_bot {
    width: 500px;
    height: 30px;
    margin-left: 5px;
    margin-top: 15px;
}
.b_button {
    border-radius: 5px;
    color: rgb(38, 19, 49);
    font-size: 14px;
    margin-left: 20px;
    width: 30px;
    height: 30px;
}