.header {
    height: 40px;
    background: #444;
}
.header header {
    width: 1200px;
    line-height: 40px;
    margin: 0 auto;
}
.header header a {
    margin: 0 20px;
    color: white;
    font-size: 14px;
    text-decoration: none;
}
.header header img {
    float: left;
    margin-top: 5px;
    width: 120px;
}
.head_inp {
    float: left;
    margin-left: 30px;
}
.head_inp input {
    border-radius: 5px;
    width: 200px;
}